import React, { FC, useEffect } from "react";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import { Stack, Button, Box, FormControlLabel } from "@mui/material";
import MainInput from "../mainInput/MainInput";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";

import ImgCheckBox from "../../assets/icons/check-box.svg";
import CheckBoxChecked from "../../assets/icons/check-box-checked.svg";
import useMediaQuery from "@mui/material/useMediaQuery";

interface FormValues {
  name: string;
  surname: string;
  phone: string;
  companyName: string;
  otherField: string;
  email: string;
  agreeToTerms: boolean;
}

const Registration: FC = () => {
  const initialValues: FormValues = {
    name: "",
    surname: "",
    phone: "",
    companyName: "",
    otherField: "",
    email: "",
    agreeToTerms: false,
  };

  const sm = useMediaQuery("(max-width:900px)");
  const xs = useMediaQuery("(max-width:600px)");

  const validationSchema = Yup.object({
    name: Yup.string().required("  '"),
    email: Yup.string()
      .email(" email")
      .required("  '"),
    surname: Yup.string().required("  '"),
    phone: Yup.string()
      .transform((value) => value.replace(/\D/g, ""))
      .min(12, "    12 ")
      .required("  '"),
    companyName: Yup.string().required("  '"),
    otherField: Yup.string(),
    agreeToTerms: Yup.boolean().oneOf([true], "You must accept the terms"),
  });

  const onSubmit = (values: FormValues) => {
    console.log(values);
  };

  useEffect(() => {}, []);
  return (
    <Container
      sx={{
        paddingBottom: xs ? "75px" : sm ? "110px" : "145px",
      }}
    >
      <Box
        sx={{
          maxWidth: "455px",
          width: "100%",
          margin: "0 auto",
        }}
      >
        <>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
          >
            {({ dirty, isValid, values, errors, touched, setFieldValue }) => (
              <Form>
                <MainInput name="name" placeholder="*" label="*" />

                <MainInput
                  name="surname"
                  placeholder="*"
                  label="*"
                />

                <MainInput
                  name="companyName"
                  placeholder="*"
                  label="*"
                />

                <MainInput
                  name="phone"
                  placeholder="  *"
                  label="  *"
                  isMask={true}
                />

                <MainInput
                  name="email"
                  placeholder="  *"
                  label="  *"
                />

                <MainInput
                  name="otherField"
                  placeholder=" ,             "
                  label=" "
                  multiline
                />

                <Stack direction="row" style={{ paddingBottom: "40px" }}>
                  {values.agreeToTerms ? (
                    <img
                      onClick={() => {
                        setFieldValue("agreeToTerms", !values.agreeToTerms);
                      }}
                      src={CheckBoxChecked}
                      alt=""
                      width="24"
                      height="24"
                    />
                  ) : (
                    <img
                      onClick={() => {
                        setFieldValue("agreeToTerms", !values.agreeToTerms);
                      }}
                      src={ImgCheckBox}
                      alt=""
                      width="24"
                      height="24"
                    />
                  )}

                  <Field
                    type="checkbox"
                    name="agreeToTerms"
                    style={{ display: "none" }}
                  />

                  <Typography
                    variant="body2"
                    color="white"
                    sx={{
                      fontSize: xs ? "0.813rem" : "0.938rem",
                      fontLine: "1.172rem",
                      fontFamily: "'ManropeMedium', sans-serif",
                      paddingLeft: "19px",
                    }}
                  >
                    ,    {" "}
                    <span style={{ color: "#FCB02D" }}>
                      {" "}
                        Uspacy
                    </span>
                  </Typography>
                </Stack>

                <Stack
                  direction="row"
                  alignItems="center"
                  justifyContent="center"
                >
                  <Button
                    variant="contained"
                    type="submit"
                    sx={{
                      textTransform: "none",
                      backgroundColor: "#452AF4",
                      color: !isValid || !dirty ? "#FFFFFF66" : "#FFFFFF",
                      borderRadius: "100px",
                      padding: "10px 20px",
                      boxShadow: "none",
                      height: xs ? "52px" : sm ? "55px" : "58px",
                      fontFamily: "'MontserratMedium', sans-serif",
                      fontSize: xs ? "0.75rem" : sm ? "0.875rem" : "1rem",
                      fontLight: "0.885rem",
                      width: xs ? "145px" : sm ? "180px" : "213px",
                      "&:hover": {
                        backgroundColor: "#FCB02D",
                        color: "#FFFFFF",
                      },
                      "&.Mui-disabled": {
                        backgroundColor: "#452AF4",
                        color: "#FFFFFF66",
                      },
                    }}
                  >
                    
                  </Button>
                </Stack>
              </Form>
            )}
          </Formik>
        </>
      </Box>
    </Container>
  );
};

export default Registration;
